.trainers_container{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 3rem;
}
.card.trainer{
    padding: 1rem;
}
.trainer_img{
    border-radius: 0 2rem;
    overflow: hidden;
}
.trainer h3{
    margin-top: 2rem;
}
.trainer p{
    margin-top: 0.5rem;
}
.trainer_socials{
    display: flex;
    gap: 1rem;
    justify-content: center;
    align-items: center;
    margin: 1rem 0;
}
.trainer_socials a{
    font-size: .7rem;
    background: var(--color-gray-600);
    padding: .8rem;
    border-radius: .7rem;
    transition: var(--transition);
    display: grid;
}
.trainer:hover a{
    background: var(--color-gray-500);
}

@media screen and (max-width: 1024px){
    .trainers_container {
        grid-template-columns: 1fr 1fr;
        gap: 2rem;
    }
}
@media screen and (max-width: 600px){
    .trainers_container {
        grid-template-columns: 1fr;
        gap: 1.5rem;
    }
    .card.trainer{
        width: 90%;
        margin-inline: auto
    }
}